<template>
    <div class="mw-page page storage" v-nav="{hideNavbar:true}">
        <div class="mw-page-header">
            <i class="left icon ion-ios-arrow-back" @click="$router.go(-1)"></i>
            <span>物料类型列表</span>
            <i class="right" @click="addType = true">新增</i>
        </div>
        <div class="mw-page-content">
          <div>
            <swipe-item  :obj="item" swipeItemText="删除"
                       v-on:SwipeItemClick="itemDel(item)" v-for="item in storehouse_list">
              <span>{{item.mat_type_name}}</span>
            </swipe-item>
          </div>
        </div>
      <mw-prompt :show="addType" :okFunction="add" v-model="newType" v-on:hidePop="addType = false" placeholder="请输入物料类型名称"></mw-prompt>
        <!--<div class="mw-page-footer">-->
            <!--<div class="btns">-->
                <!--<div class="btn" @click="">-->
                    <!--新增-->
                <!--</div>-->
            <!--</div>-->
        <!--</div>-->
    </div>
</template>

<script>
  import { storageMats,storageMatModify,storageMatDel } from '@/api/storage/baseInfo'
  import SwipeItem from "../../../components/swipeItem";
  import MwInput from "../../../components/mw-input";
  import MwPrompt from "../../../components/mwPrompt";
    export default{
      name:'matTypes',
      components: {
        MwPrompt,
        MwInput,
        SwipeItem},
      data(){
        return {
          storehouse_list:[],
          action:'check',
          addType:false,
          newType:''
        }
      },
      activated(){
        this.getData()
      },
      methods: {
        showPop(){

        },
        getData(){
          const reqData = {
            is_paging:0
          }
          storageMats(reqData).then(res=>{
            console.log(res)
            this.storehouse_list = res.mat_type_list
          }).catch(error=>{
            $toast.show(error.description)
          })
        },
        //查看详情
        itemClick(item) {
          this.$router.forward('/storage/matTypeDetail/'+this.action+'/'+JSON.stringify(item))
        },
        add(){
          if (!this.newType||!this.newType.trim()){
            $toast.show('类型名称不能为空')
            return
          }
          storageMatModify({mat_type_info:{mat_type_name:this.newType}}).then(res=>{
            $toast.show('保存成功')
            this.addType = false
            this.newType =''
          }).catch(error=>{
            $toast.show(error.description)
          })
        },
        //删除仓库
        itemDel(item) {
          if (item.is_default=='1') {
            $toast.show('缺省物料类型无法删除')
            return
          }
          $dialog.confirm({
            theme:'ios',
            title:'提示',
            content:'确定要删除吗?',
            okText:'确定',
            cancelText:'取消'
          }).then(ok=>{
            if(ok) {
              storageDel({
                store_house_id:item.store_house_id
              }).then(res=>{
                this.storehouse_list = this.storehouse_list.filter(o=>{
                  return o.store_house_id != item.store_house_id
                })
              }).catch(error=>{
                $toast.show(error.description)
              })
            }
          }).catch(error=>{
            $toast.show(error.description)
          })
        }

      }
    }
</script>

<style lang="scss" scoped>

</style>



